<template>
  <div>
    <section class="header">
      <div @click="timeClick" :style='{"color":(selcetFlag ? "#2c3e50":"rgba(127, 129, 132, 0.582)")}'>
        时间
        <img :src="upImg" />
        <img :src="downImg" />
      </div>
      <span>|</span>
      <div @click="stateClick" :style='{"color":(selcetFlag ? "rgba(127, 129, 132, 0.582)":"#2c3e50")}'>
        全部状态
        <img src="@/assets/down.png" />
      </div>
    </section>
    <section class="record">
      <div class="r_content" v-for="(item,index) of item" :key="index">
        <div
          class="top"
          :style="{'background-color': (item.state==1 ? '#50A3F1':'#E84A00')}"
        >{{item.name}}</div>
        <div class="recordContnet">
          <div>
            <p>客户手牌：{{item.kNum}}</p>
            <p>技师手牌：{{item.jNum}}</p>
          </div>
          <div>上钟时间：{{item.upTime}}</div>
          <div>
            <p>下钟时间：{{item.downTime}}</p>
            <span
              :style="{'background-color': (item.state==1 ? '#50A3F1':'#E84A00')}"
            >{{item.state == 1 ? "已完成":"已下钟"}}</span>
          </div>
          <div class="remark">
            <p>{{item.remark}}</p>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: true,
      selcetFlag: true,
      upImg: require("@/assets/d_s.png"),
      downImg: require("@/assets/u.png"),
      item: [
        {
          name: "养生足疗（60分钟）",
          jNum: "999",
          kNum: "002",
          upTime: "2019-12-17 10:30",
          downTime: "2019-12-17 10:30",
          state: "1",
          remark: "暂无备注信息"
        },
        {
          name: "养生足疗（60分钟）",
          jNum: "999",
          kNum: "002",
          upTime: "2019-12-17 10:30",
          downTime: "2019-12-17 10:30",
          state: "2",
          remark: "异常状态，客户提前下钟"
        },
        {
          name: "养生足疗（60分钟）",
          jNum: "999",
          kNum: "002",
          upTime: "2019-12-17 10:30",
          downTime: "2019-12-17 10:30",
          state: "1",
          remark: "暂无备注信息"
        }
      ]
    };
  },
  methods: {
    timeClick() {
      if (this.flag) {
        this.upImg = require("@/assets/d.png");
        this.downImg = require("@/assets/u_s.png");
      } else {
        this.upImg = require("@/assets/d_s.png");
        this.downImg = require("@/assets/u.png");
      }
      this.flag = !this.flag;
      this.selcetFlag = !this.selcetFlag;
    },
    stateClick(){
this.selcetFlag = !this.selcetFlag;
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  @include wh(100%, 100%);
  @include fr(space-around);
  @include sc(0.8rem, $fColor);
  background-color: $fc;
  div {
    @include wh(100%, 100%);
    margin: 0.8rem;
    color: $Tline;
    img {
      @include wh(0.9rem, 0.9rem);
    }
  }
  div:first-child {
    @include fr(center);

    img:last-child {
      margin-left: 1.3rem;
      position: absolute;
      z-index: 0;
    }
  }
  div:last-child {
    @include fr(center);
  }
  span {
    color: $Tline;
  }
}
.record {
  .r_content {
    background: $fc;
    margin: 0.8rem 0.8rem;
    padding-bottom: 0.5rem;
    border-radius: 0.5rem;
    font-size: 0.9rem;
  }
  .top {
    @include fr(space-between);
    color: $fc;
    margin: 0rem;
    padding: 0.5rem;
  }
  .recordContnet {
    @include fc(center);
    @include wh(100%, 100%);
    padding: 0rem 0.8rem;
    div {
      @include fr(space-between);
      @include wh(100%, 100%);
      @include sc(0.8rem, $fColor);
      margin-top: 0.6rem;
      span {
        padding: 0.5rem 0.8rem;
        color: $fc;
        border-radius: 0.3rem;
      }
    }
    .remark {
      border-top: 0.02rem solid $F5;
      padding: 0.6rem 0.2rem;
      margin-top: 0.2rem;
    }
  }
}
</style>